<template>
  <div>
    <table>
      <h3>Cart:</h3>
      <tr>
        <th>Name</th>
        <th>Price</th>
        <th>Qty</th>
        <th></th>
      </tr>

      <tr v-for="(item,index) in Cart" :key="index">
        <td>{{ item.name }}</td>
        <td>{{ item.price | formatMoney('',type) }}</td>
        <td>{{ item.qty }}</td>
        <td>
          <button @click="Cart.splice(index,1)">Delete</button>
        </td>
      </tr>

      <tr id="total">
        <td>Total:</td>
        <td>{{ total = Cart.reduce( (total, item) => { return total + item.price*item.qty},0 ) | formatMoney('VND',type) }}</td>
        <td>{{ Math.round(total/23000*100)/100 | formatMoney('USD',type) }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: ["Cart", "type"],
  filters: {
    formatMoney(x, unit, type) {
     return (
        x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, type) + " " + unit
      );
    }
  }
};
</script>

