<template>
  <div>
    <table>
      <h3>Products:</h3>
      <tr>
        <th>Name</th>
        <th>Price</th>
        <th>Qty</th>
        <th></th>
      </tr>

      <tr v-for="(product,index) in products" :key="index">
        <td>{{ product.name }}</td>
        <td>{{ product.price | formatMoney(type) }}</td>
        <td>
          <input type="number" name="quantity" v-model="product.qty" min="1">
        </td>
        <td>
          <button
            @click="Cart.push({name: product.name, price: product.price, qty: product.qty})"
          >Add to Cart</button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: ["products", "Cart", "type"],
  filters: {
    formatMoney(x, type) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, type);
    }
  }
};
</script>


